.work {
  padding: 5%;;
  background-color: #24143e;
}



.work-experience {
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #ddd;
}

.work-experience h3 {
  font-size: 20px;
  /* color: #333; */
  margin-bottom: 10px;
}

.company {
  margin-bottom: 15px;
  color: #92b6d3 !important;
  font-weight: bolder;
}

.work-experience .date {
  font-style: italic;
  font-size: 14px;
  /* color: #888; */
}

.skills-list {
  list-style: none;
  padding: 0;
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills-list li {
  margin: 5px;
  padding: 8px 15px;
  font-size: 14px;
  /* color: #fff; */
  background-color: #333;
  border-radius: 20px;
  transition: background-color 0.3s ease;
}

.skills-list li:hover {
  background-color: #555;
}
.project-tags {
  display: flex;
  max-width: 60%;
  flex-wrap: wrap;
}

.tag {
  padding: 4px 8px;
  background-color: rgb(110, 110, 212);
  border-radius: 4px;
  margin-right: 6px;
  margin-bottom: 4px;
  animation: fadeInUp 0.6s ease forwards;
  transition: background-color 0.3s ease;
}

.tag:hover {
  background-color: rgb(80, 80, 156);
}

@media (max-width: 768px) {
  .work {
    padding: 3%;
    padding-top: 12%;
  }
}